article p {
    text-align: justify;
    hyphens: auto;
}
img.inline.float-right {
    @apply m-8 mr-0 mt-0;
    max-width: 40%;
    object-fit: cover;
}

img.inline.float-left {
    @apply m-8 ml-0 mt-0;
    max-width: 40%;
    object-fit: cover;
}

ul li {
    font-size: 1.25rem;
    font-weight: 300;
    &:not(:last-of-type) {
        @apply mb-4;
    }
}